import { useEffect, useState } from 'react';
import './App.css';
const { hands_names, cascades_names, patterns_list } = require('./tricks');


function App() {
  // const trick_selected = JSON.parse(decodeURIComponent(GetUrl('trick') || "null"));
  const [trick_selected, set_trick_selected] = useState(null);

  return (
    <div className="App">
      <h2>Entangled Juggling Patterns</h2>

      <div id="table_container">
        <table style={{width: "100px", border: "1px solid black"}}>
          <tr>
            <td/>
            <td/>
            <th colspan="3">hands</th>
          </tr>
          <tr>
            <td/>
            <td/>
            {Object.entries(hands_names).map(([hands, hands_text]) => {
              return  (
              <th scope="col">
                {hands_text}
                <img src={require(`../assets/icons/${hands}.png`)} style={{height: "25px", width: "80px", margin: "5px 8px 0px 8px"}}/>
              </th>
              );
            })}
          </tr>

          {Object.entries(Object.entries(cascades_names)).map(([index, [cascades, cascade_text]]) => {
            return  (
              <tr>
                {index == 0 && (
                  <th rowspan="3">
                    <p style={{writingMode: "sideways-lr", paddingRight: "10px"}}>cascades</p>
                  </th>
                )}
                <th scope="row">
                  <img src={require(`../assets/icons/${cascades}.png`)} style={{height: "20px", width: "80px", margin: "0px 8px 0px 8px"}}/>
                  {cascade_text}
                </th>
                {Object.entries(hands_names).map(([hands, hands_text]) => {
                  const highlight = trick_selected && trick_selected.hands === hands && trick_selected.cascades === cascades;
                  return  (
                    <td 
                      style={{
                        border: "1px solid black", 
                        height: "100px",
                        width: "100px",
                        cursor: "pointer",
                        backgroundColor: highlight ? "#CCCCCC" : "#FFFFFF"}}
                      onClick={() => set_trick_selected({type: "pattern", hands: hands, cascades: cascades})}
                    >
                      <div>
                        <img src={require(`../assets/icons/${cascades}.png`)} style={{height: "20px", width: "80px", margin: "0px 8px 0px 8px"}}/>
                        <img src={require(`../assets/icons/${hands}.png`)} style={{height: "25px", width: "80px", margin: "0px 8px 0px 8px"}}/>
                      </div>
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </table>
      </div>
        
      {trick_selected && (
        <div id="trick_container" classname="trickContainer">
          
          
        </div>
      )}
    </div>
  
  );
}

export default App;
