
export const hands_names = {
  aabb: "side by side",
  abab: "crossed",
  abba: "in and out",
}

export const cascades_names = {
  crossed: "crossed",
  separated_parallel: "separated parallel",
  separated_mirrored: "separated mirrored",
}

