export function generateName(hands, cascades){
  return `${hands_names[hands]} hands, ${cascades_names[cascades]} cascades`
}

export const patterns = Object.assign({}, ...patterns_list.map((p) => ({[generateName(p.hands, p.cascades, p.symmetry)]: p})));

